@import '~scss/variables';
@import '~scss/custom-properties';

.ff-panel-card {
  ff-img {
    display: block;
    position: relative;
    overflow: hidden;
    padding-top: 50%;
    margin-bottom: 1rem;

    img, .img-hq {
      position: absolute;
      left: 0;
      top: 50%;
      width: 100%;
      height: auto;
      transform: translateY(-50%);
    }

    &.ff-img-placeholder {
      &::before {
        position: absolute;
        left: 0;
        top: 0;
      }
    }
  }

  .ff-panel-body {
    display: flex;
    flex-direction: column;
  }

  ff-btn-group {
    display: flex;
    align-items: flex-end;
    //margin: 0 0 0 auto;
    margin-left: auto;
  }
}
