@import '~scss/variables';
@import '~scss/custom-properties';
@import '~scss/mixins';

.sr-center-address, .sr-center-phone {
  margin-bottom: .25em;
}

.sr-center-address, .sr-center-phone, .sr-center-email {
  ff-icon {
    width: 1.2rem;
    text-align: center;
    font-size: 1.2rem;
  }
}

.sr-center-phone {
  ff-icon {
    vertical-align: middle;
  }
}

.sr-center-email {
  ff-icon {
    font-size: 1em;
  }
}
