@import '~scss/variables';

res-bar {
  display: block;
  position: relative;
  width: 100%;
  height: 18px;
  min-height: 18px;
  margin-bottom: 1.1rem;
  border: 1px solid;
  background-color: #000;

  .availability {
    position: absolute;
    top: 0;
    background-color: var(--brand-color);
    height: 100%;
  }

  .from, .to {
    position: absolute;
    top: 100%;
    margin-top: .3rem;
    font-size: .9rem;

    sup {
      top: -.2em;
      display: none;
    }
  }

  .from {
    left: 0;
    transform: translateX(-50%);
  }

  .to {
    right: 0;
    transform: translateX(50%);
  }

  & > .availability:first-of-type .from sup, & > .availability:last-of-type .to sup {
    display: inline;
  }
}
