.sr-units {
  .legend {
    font-size: .75rem;
    font-weight: bold;
    text-transform: uppercase;

    .available, .unavailable {
      margin: 1px 0;

      &::before {
        content: '';
        display: inline-block;
        width: 12px;
        height: 12px;
        margin: 0 5px 0 0;
        vertical-align: text-bottom;
      }
    }

    .available::before {
      background-color: var(--brand-color);
      border: 1px solid;
    }

    .unavailable::before {
      background-color: #000;
    }
  }
}
