@import '~scss/variables';
@import '~scss/custom-properties';

.sr-category-panel {
  max-width: 670px;
  margin: auto;
}

.sr-swiper-wrapper {
  display: block;
  position: relative;
  overflow: hidden;
  padding-top: 50%;
}

.sr-swiper-content {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 50%;
  transform: translateY(-50%);

  .swiper-container {
    height: 100%;
  }

  .swiper-slide {
    display: flex;
    align-items: center;
  }

  .swiper-thumbnail {
    display: flex;
    align-items: center;
    position: absolute;
    width: 100%;
    height: 100%;
    will-change: opacity;
    transition: opacity .1s .5s;

    ff-img {
      width: 100%;

      &.ff-img-error {
        display: flex;
        flex-flow: column;
        min-height: 100%;

        &::before {
          flex: 100%;
        }
      }
    }
  }
}

swiper:not(.loading) {
  .swiper-thumbnail {
    opacity: 0;
  }
}

.sr-category-products {
  h3 {
    font-size: inherit;
    margin: 1rem 0 .25rem 0;
    text-transform: uppercase;
    font-weight: bold;
  }

  .ff-row {
    & > div:last-of-type {
      display: flex;
      align-items: flex-end;
      justify-content: flex-end;
      font-weight: bold;
    }
  }
}

.sr-category-details {
  .tab-pane > div:first-of-type > h3 {
    margin-top: 0;
  }
}
