@import '~scss/variables';
@import '~scss/custom-properties';

.sr-time-panel {
  max-width: 570px;
  margin: auto;

}

.sr-time-container {
  max-width: 480px;
  margin: auto;

  .staff-only {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 100%;
    padding-top: 55px;
    text-align: center;
    text-transform: none;
    font-weight: normal;
    background: rgba(255,255,255,0.9);
    z-index: 5;
  }
}

.sr-time {
  .clear-selection {
    display: none;
  }

  .slot.optional {
    display: none;
  }
}

.sr-time-email {
  margin: 1rem 0 .5rem 0;
}
