@import '~scss/variables';
@import '~scss/custom-properties';

.tabs-block ul.nav-pills {
  margin: 0;
  padding: 0;
  text-align: center;
  list-style: none;

  li {
    float: none;
    display: inline-block;
    position: relative;
  }

  a {
    display: block;
    background: none;
    color: $text-color;
    min-width: 100px;
    text-align: center;
    padding: 1.33rem 1rem;
    cursor: pointer;
    text-decoration: none;

    &:hover, &:focus {
      background: none;
    }
  }

  .active a {
    color: inherit;
    font-weight: bold;
    cursor: auto;

    &::after, &::before {
      content: '';
      position: absolute;
      left: 50%;
      bottom: 0;
      width: 0;
      height: 0;
      border: 0 solid transparent;
    }
    &::before {
      margin-left: -9px;
      border: 1px solid $ff-control-border-color;
      border-width: 0 9px 9px 9px;
      border-top-color: transparent;
      border-left-color: transparent;
      border-right-color: transparent;
    }
    &::after {
      bottom: -1px;
      margin-left: -8px;
      border-width: 0 8px 8px 8px;
      border-bottom-color: #fff;
    }
  }
}

.tab-pane {
  max-width: 480px;
  margin: 0 auto;
}

.tab-content {
  padding: 1rem 1rem .333rem 1rem;
  border: 1px solid $ff-control-border-color;
  border-radius: .5rem;
}

.tab-content > .tab-pane {
  display: none;
}

.tab-content > .active {
  display: block;
}
